.upper_head {
  background: $heavy_grey;
}

header {
  background: $normal_grey;
}

.brand {
  font-family: 'Gill Sans Nova con semibold', sans-serif;
  font-size: 1rem;
  text-transform: uppercase;

}

header .brand {
  color: $dark_grey;
}

header .head {
  background: $heavy_grey;
}

.logo {
  padding: 0 0.5rem 0 0;
}

.head a:hover {
  text-decoration: none;
}

.nav-primary {
  font-family: 'Aka-Acid-Sunn', sans-serif;
  font-size: 3.1rem;
  color: $dark_grey;
}

.nav-primary a {
  color: $dark_grey;
}


.head .nav-primary li {
  padding: 0 3rem 0 0 ;
}

header ul {
  list-style: none;
  display: flex;
  flex-wrap: wrap;
  padding: 0.5rem 0 0 0;
  margin-bottom: 0;
}

header ul li {
  padding: 0 0 0 1rem;
}

header a {
  color: $light_grey;
}

header .top-bar .btn {
      border: 0px solid transparent;
}

.head label {
  width: 100%;
}

.search-form .search-field {
    width: 100%;
}

.head .search-submit {
  display: none;
}
