@font-face {
    font-family: 'Aka-Acid-Sunn';
    src: url('../fonts/Aka-Acid-Sunn.eot');
    src: url('../fonts/Aka-Acid-Sunn.eot?#iefix') format('embedded-opentype'),
        url('../fonts/Aka-Acid-Sunn.woff2') format('woff2'),
        url('../fonts/Aka-Acid-Sunn.woff') format('woff');
    font-weight: 300;
    font-style: normal;
}

